const cutdownTime = 60;
export default {
	data() {
		return {
		
			ispwd: false,
			mobile: '',
			noadjust: false,
			code: '',
			keybord: 0,
			mobile_focus: false,
			password_focus: false,
			code_focus: false,
			
			lol: '',
			password: '',
			item: true,
			sel_tab: '1',
			sendCodeLoading: false, // 正在发送短信
			cutdownIng: false, // 正在倒计时中，
			cutDownTiemr: '', // 倒计时定时器
			sendBtnText: '获取验证码',
			disabled: false,
			text: '150',
			ktxStatusHeight: '',
			navigationHeight: ''

		}
	},
	onLoad() {


		let systemInfo = uni.getSystemInfoSync()
		// px转换到rpx的比例
		let pxToRpxScale = 750 / systemInfo.windowWidth;
		// 状态栏的高度
		let ktxStatusHeight = systemInfo.statusBarHeight * pxToRpxScale
		// 导航栏的高度
		let navigationHeight = 44 * pxToRpxScale
		this.navigationHeight = navigationHeight + 'rpx';
		console.log(ktxStatusHeight)
		console.log(navigationHeight)


	},

	computed: {
		style() {
			let _style = `height: ${this.navigationHeight}px;`
			return _style
		},
		submitBtnStatus() {
			if (this.sel_tab === '1') {
				if (this.mobile && this.code) return true;
			} else {
				if (this.mobile && this.password) return true;
			}
			return false;
		},
		title_height() {
			var that = this;
			//console.log(this.text)
			if (this.mobile_focus || this.code_focus || this.password_focus) {
				that.item = false;
			}
			if (this.mobile_focus == false && this.code_focus == false && this.password_focus == false) {
				that.item = true;
			}
		},

	},

	/* onShow() {
		var that=this;
		console.log('onsho34334w')
		uni.onKeyboardHeightChange(res => {
			console.log('onshow')
			that.lol = res.height
			console.log(res.height,that.phonefocus,that.passwordfocus)
			if (that.item==false && res.height== 0  && that.phonefocus==true && that.passwordfocus==false ) {
				console.log('0')
				
				that.item=true
				}
			else if ( res.height!= 0  && that.phonefocus==true && that.passwordfocus==false ) {
				 console.log('1')
				that.item=false
			}
			else if ( res.height!= 0 && that.phonefocus==false && that.passwordfocus==true ) {
				console.log('2')
				that.item=false
			}
			else{
				that.item=true
			}
		});
		
	}, */
	onShow() {
		var that = this;
		uni.onKeyboardHeightChange(res => {
			console.log(res.height)
			if (res.height == 0) {
				that.itme = true
			} else {
				that.itme = false
			}
		})
	},
	methods: {
		
	
		// 返回
		backFront() {
			uni.navigateBack({
				delta: 1
			})
		},
		nofocusbox() {
			this.item = true
			this.lol = 0
		},
		
		getCodeBtn() {
			if (this.sendCodeLoading || this.cutdownIng) return;
			// if (this.disabled) return false;
			if (!this.mobile) {
				uni.showToast({
					title: '请输入手机号',
					icon: 'none',
					mask: true,
					duration: 2000
				});
				return false;
			}
			const myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
			if (!myreg.test(this.mobile)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					mask: true,
					duration: 2000
				});
				return false;
			}
			const _this = this;
			uni.request({
				method: 'POST',
				data: {
					mobile: _this.mobile
				},
				//短信接口
				url: _this.url + 'users/login/sms/',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				success(res) {
					//根据code判断
					console.log(res);
					var ocode = res.statusCode;
					if (ocode == 200) {
						_this.iscode = res.data.data || _this.codess;
						_this.disabled = true;
						let surTime = cutdownTime;
						_this.cutdownIng = true;
						_this.sendBtnText = `${surTime}s`;
						_this.cutDownTiemr = setInterval(() => {
							surTime--;
							_this.sendBtnText = `${surTime}s`;
							if (surTime <= 0) {
								clearInterval(_this.cutDownTiemr);
								_this.disabled = false;
								_this.cutdownIng = false;
								_this.sendBtnText = '重新获取';
								_this.cutDownTiemr = '';
							}
						}, 1000);
						/* var num = 61;
						var timer = setInterval(function() {
							num--;
							if (num <= 0) {
								clearInterval(timer);
								_this.codename = '重新发送';
								_this.disabled = false;
							} else {
								_this.codename = num + 's';
								_this.disabled = true;
							}
						}, 1000); */
					} else if (ocode == 400) {
						uni.showToast({
							title: '用户不存在',
							icon: 'none',
							duration: 2000
						});
						return false;
					} else if (ocode == 411) {
						uni.showToast({
							title: '操作太频繁，请稍候重试',
							icon: 'none',
							duration: 2000
						});
						return false;
					}
				}
			});
		},
		/**
		 * 选择tab
		 * @param {Object} tab
		 */
		handleSelectTab(tab) {
			this.sel_tab = tab;
			this.item = true
		},

		/**
		 * 跳转忘记密码页面
		 */
		handleToWJMA() {
			uni.navigateTo({
				url: '../getBackPassword/getBackPassword'
			});
		},
		inot() {
			this.fonter = false;
		},
		/**
		 * 提交
		 */
		handleSubmit() {
			const _this = this;
			if (this.mobile == '') {
				uni.showToast({
					icon: 'none',
					title: '您还没有输入手机号码'
				});
				return;
			}
			// if (this.submitBtnStatus) {

			const myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
			if (!myreg.test(this.mobile)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					mask: true,
					duration: 2000
				});
				return false;
			}
			//const params = { mobile: this.mobile };
			if (this.sel_tab === '1') {
				// 快速登录
				if (!this.code) {
					uni.showToast({
						title: '请输入验证码',
						icon: 'none',
						duration: 2000
					});
					return false;
				}
				//params.code = this.code;
			} else {
				// 密码登录
				if (this.password == "") {
					console.log(this.password)
					uni.showToast({
						title: '请输入密码',
						icon: 'none',
						duration: 2000
					});
					return false;
				}
				//params.password = this.password;
			}
			if (this.sel_tab === '1') {
				uni.request({
					url: this.url + 'users/login/',
					method: 'POST',
					data: {
						mobile: _this.mobile,
						code: _this.code
					},
					headers: {
						'Content-Type': 'application/json'
					},
					success: res => {
						console.log(res);
						
						if (res.statusCode == 400) {
							uni.showToast({
								title: '验证码不正确',
								icon: 'none'
							});
						}
						if (res.statusCode == 401) {
							uni.showToast({
								title: '请用手机短信快速登录,并设置密码',
								icon: 'none',
								duration: 3000
							});
						}
						if (res.statusCode == 402) {
							uni.showToast({
								title: '用户名或密码错误',
								icon: 'none',
								duration: 3000
							});
						}
						if (res.statusCode == 412) {
							uni.showToast({
								title: '请用手机短信快速登录,并设置密码',
								icon: 'none'
							});
						}
						if (res.statusCode == 200) {
							let setTime = new Date().getTime();
							uni.setStorageSync('nowtime', setTime);
							uni.setStorageSync('phone', _this.mobile);
							uni.setStorageSync('token', res.data.data);
							console.log(res.data.data);
							console.log(_this.mobile);
							_this.global_.phone = uni.getStorageSync('phone');
							_this.global_.token = uni.getStorageSync('token');
							uni.reLaunch({
								url: '../index/index'
							});
						}
					}
				});
			} else if (this.sel_tab === '2') {
				uni.request({
					url: this.url + 'users/login/',
					method: 'POST',
					data: {
						mobile: _this.mobile,
						password: _this.password
					},
					headers: {
						'Content-Type': 'application/json'
					},
					success: res => {
						console.log(res);
						
						if (res.statusCode == 401) {
							uni.showToast({
								title: '请用手机短信快速登录,并设置密码',
								icon: 'none',
								duration: 3000
							});
						}
						if (res.statusCode == 402) {
							uni.showToast({
								title: '用户名或密码错误',
								icon: 'none',
								duration: 3000
							});
						}
						if (res.statusCode == 200) {
							let setTime = new Date().getTime();
							uni.setStorageSync('nowtime', setTime);
							uni.setStorageSync('phone', _this.mobile);
							uni.setStorageSync('token', res.data.token);
							console.log(res.data.token);
							console.log(_this.mobile);
							_this.global_.phone = _this.mobile;
							_this.global_.token = uni.getStorageSync('token');
							uni.reLaunch({
								url: '../index/index'
							});
						}
						if (res.statusCode == 412) {
							uni.showToast({
								title: '请用手机短信快速登录,并设置密码',
								icon: 'none'
							});
						}
					}
				});
			}
			// }
		}
	}
};
